import * as okayapi from '../../../components/okayapi/okayapi.js';


export default {
	data() {
		return {
			title: 'QrCode',
			first: '',
			data: '',
		}
	},
	methods: {
		pngOkay: function() {
			var params = {
				s: "Ext.QrCode.Png", // 必须，待请求的接口服务名称
				data: this.data,
			};
			var str = '';
			for(let key in okayapi.enryptData(params)){
				str += key+ "=" + okayapi.enryptData(params)[key] + '&';
			}
			this.first = this.okayapiHost + '?' + str;
		},
	}
}
